import React from 'react';

const Button = ({ label }) => {
  return (
    <div
      style={{
        position: 'absolute', // Define a posição absoluta
        top: '75%', // Centraliza verticalmente
        left: '50%', // Centraliza horizontalmente
        transform: 'translate(-50%, -50%)', // Centraliza no centro do pai
        zIndex: '6'
      }}
    >
      <button
        style={{
          width: '290px',
          height: '72px',
          borderRadius: '24px',
          background: '#FF9385',
          border: 'none',
          cursor: 'pointer',
          fontSize: '16px', // Pode ajustar o tamanho da fonte conforme necessário
          color: 'white', // Pode ajustar a cor do texto conforme necessário
          fontFamily: 'Signika'
        }}
      >
        {label}
      </button>
    </div>
  );
}

export default Button;
