import React, { useState } from "react";
import './tela.css'
import Imagem from './img/foto2.png'
import Tela3 from '../tela3/tela3.js'

const Tela2 = () => {

    const [currentPage, setCurrentPage] = useState('Tela2');

    const goToTela2 = () => {
        setCurrentPage('Tela3');
    };

    if (currentPage === 'Tela3') {
        return <Tela3 />;
    }

    return(
        <>
       <div className="logo">Seu Nutri</div>
<div className="header">
    <div className="container-counter">
    <p>Termos</p> <p>2/4</p>
    </div>
</div>  



<div className="desc2">
    <p>Antes de continuarmos, certifique-se de ler e </p>
    <div className="margin-dif"><p>aceitar nossos termos de serviço e  política de 
                privacidade.</p></div>
</div>

<div className="image-container22">
    <img src={Imagem} />
</div>

<div className="button2-container">
    <button className="butaozinho" onClick={goToTela2} >ACEITAR TERMOS</button>
</div>

<div className="texto1-container">
        <p className="termos2">
           Termos de Serviço
        </p>
</div>


        </>
    )
}

export default Tela2;