import React from 'react';
import './semdieta.css'
import SemDietaImg from './img/semdieta.png'

const SemDieta = () => {
    return(
        <>

<div class="suadieta">
        <p class="SUA-DIETA">SUA DIETA</p>
    </div>

    <div class="refeicoes-dicas">
        <div class="refeicoes">REFEIÇÕES</div>
        <div class="dicas">DICAS</div>
    </div>

    <div class="imagem">
        <img src={SemDietaImg}/>
    </div>

    <div class="suadieta">
        <p class="SUA-DIETA">SEM     DIETA</p>
    </div>
    <div class="texto-dieta">
        <p class="textodieta">Notamos que ainda não fez sua dieta,<br/> clique no botão abaixo e comece já a sua!</p>
    </div>

    <div class="button-container">
        <button class="start-button">FAZER MINHA DIETA</button>
    </div>



        </>
    )
}

export default SemDieta;