// src/App.js
import React, { useEffect, useState } from 'react';
import Preloader from './components/preloader/preloader.js';
import logo from './components/preloader/img/logo-branca.svg';
import Apresentacao from './pages/apresentacao/tela.js';

const App = () => {
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const img = new Image();
        img.src = logo;
        img.onload = () => {
            // Logo carregada, inicia o contador para remover o preloader
            setTimeout(() => {
                setLoading(false);
            }, 1500); // 1.5 segundos
        };
    }, []);

    return (
        <div className="App">
            {loading ? <Preloader /> : <Apresentacao />}
        </div>
    );
};

export default App;
