import React, { useState, useRef } from "react";
import './tela3.css'
import Imagem from './img/foto.png'
import Tela4 from '../tela4/tela4.js'



const Tela3 = () => {


    const estiloDoParagrafo = {
        marginTop: '20px', // Ajuste o valor conforme necessário
      };

    const estiloimagem = {
        marginTop: '20px'
    }

 
    const [selectedFile, setSelectedFile] = useState(null);
    const fileInputRef = useRef(null);
    const [currentPage, setCurrentPage] = useState('Tela3');

    const goToTela2 = () => {
        setCurrentPage('Tela4');
    };

    if (currentPage === 'Tela4') {
        return <Tela4 />;
    }

    

    const handleFileSelect = (event) => {
        const file = event.target.files[0];
        if (file) {
            setSelectedFile(file);
            // You can also perform further actions here,
            // like reading the file or uploading it to a server.
            console.log("File selected:", file.name);
            goToTela2();
        }
    };

    const handleClick = () => {
        fileInputRef.current.click();
    };


    return(
        <>
       <div className="logo">Seu Nutri</div>
<div className="header">
    <div className="container-counter">
    <p>Fotografia</p> <p>3/4</p>
    </div>
</div>  



<div className="desc">
    <p style={estiloDoParagrafo}> Vamos dar uma cara nova para seu perfil,</p>
    <div className="margin-dif"><p>Carregue uma foto para ele.</p></div>
</div>

<div className="image-container22">
    <img style={estiloimagem}src={Imagem} />
</div>

<div className="texto-container">
        <p className="account-text">Não quer adicionar uma imagem no momento?<br/><span className="mais-tarde">Aperte em "Mais Tarde"</span></p>
    </div>

    <div className="Mais-tarde-container">
        <h1><a className="maistarde ">Mais Tarde</a></h1>
    </div>
    <div className="button222-container">
            <button className="butaozinho2" onClick={handleClick}>CARREGAR FOTO</button>
            <input
                type="file"
                ref={fileInputRef}
                style={{ display: 'none' }}
                onChange={handleFileSelect}
            />
        </div>

        </>
    )
}

export default Tela3;