import React, { useState } from "react";
import './tela4.css'
import Imagem from './img/foto3.png'
import { Link } from 'react-router-dom';
import Preloader from "../../preloader/preloader";

const Tela4 = () => {

    const [isLoading, setIsLoading] = useState(false);

      const handleStartJourney = () => {
    setIsLoading(true);

    setTimeout(() => {
      window.location.href = '/home';
    }, 1500); 
  };


    return(
        <>

            {isLoading && <div><Preloader></Preloader></div>}


       <div className="logo">Seu Nutri</div>
<div className="header">
    <div className="container-counter">
    <p>Finalização</p> <p>4/4</p>
    </div>
</div>  



<div className="desc">
    <p>Obrigado por ter chegado até aqui,</p>
    <div className="margin-dif"><p>Agora você inicia sua jornada em uma vida saudável!</p></div>
</div>

<div className="image-container">
    <img src={Imagem} />
</div>
<br>

</br>

<br>

</br>

<div className="button2-container">
  <button onClick={handleStartJourney} className="next-button3">
          COMEÇAR JORNADA
        </button>
    </div>

        </>
    )
}

export default Tela4;