import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import{ CreateBrowserRouter, RouterProvider, createBrowserRouter} from "react-router-dom";
import Camera from'./pages/camera/camera.js'
import Apresentacao from './pages/apresentacao/tela.js';
import Cadastro from './pages/cadastro/cadastro.js'
import Login from './pages/login/login.js'
import Home from './pages/home/home.js'
import Perfil from './pages/perfil/perfil.js';
import SemDieta from './components/sem-dieta/semdieta.js';

const router = createBrowserRouter([
  {
    path: "/",
    element: <App/>
  },
  {
    path: "/camera",
    element: <Camera/>
  }, {
    path: "/cadastro",
    element: <Cadastro/>
  }, {
    path: "/login",
    element: <Login/>
  },
  {
    path: "/home",
    element: <Home/>
  },
  {
    path: "/perfil",
    element: <Perfil/>
  },
  {
    path: "/semdieta",
    element: <SemDieta/>
  }

])

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <RouterProvider router={router}/>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
