import React, { useState } from "react";
import './cadastro.css'
import Email from './img/email.png'
import Pincel from './img/pincel.png'
import Senha from './img/senha.png'
import Tela2 from "../../components/cadastro-telas/tela2/tela2";

const Cadastro = () => {

 const [currentPage, setCurrentPage] = useState('Cadastro');

    const goToTela2 = () => {
        setCurrentPage('Tela2');
    };

    if (currentPage === 'Tela2') {
        return <Tela2 />;
    }



    return(
        <>

    
       <div className="logo">Seu Nutri</div>
<div className="header">
    <div className="container-counter">
    <p>O Básico</p> <p>1/4</p>
    </div>
</div>  



<div className="desc3">
    <p>Preencha com atenção suas informações.</p>
    <div className="margin-dif"><p>Nem tudo pode ser alterado depois...</p></div>
</div>



<div className="inputs-container">
  <div className="input-group">
    <label className="input33-label">Nome Completo</label>
    <div className="input-wrapper">
      <input type="text" className="input-field" />
    </div>
  </div>

  <div className="input-group">
    <label className="input33-label">Insira seu E-mail</label>
    <div className="input-wrapper">
      <input type="email" className="input-field" />
    </div>
  </div>

  <div className="input-group">
    <label className="input33-label">Crie uma senha</label>
    <div className="input-wrapper">
      <input type="password" className="input-field" />
    </div>
  </div>

  <div className="input-group">
    <label className="input333-label">Digite novamente a senha</label>
    <div className="input-wrapper">
      <input type="password" className="input-field" />
    </div>
  </div>

  <div className="button-container">
    <button className="custom-button" onClick={goToTela2}>AVANÇAR</button>
  </div>
</div>


        </>
    )
}

export default Cadastro;