import React, { useState } from 'react';
import './camera.css';
import flash from './img/flash.png'
import fechar from './img/fechar.png'
import focar from './img/focar.svg'
import icon from './img/icon.png'
import reverse from './img/reverse.png'
import efeitosonoro from './img/foto.mp3'
import FooterCam from '../../components/footer-cam';
import Button from '../../components/footer-button';


const Camera = () => {
  const [fotoTirada, setFotoTirada] = useState(false);

  const playSound = () => {
    const sound = new Audio(efeitosonoro);
    sound.play();

    // Adiciona um ouvinte de evento que espera o som terminar de tocar
    sound.onended = () => {
      setFotoTirada(true); // Altera o estado para indicar que a foto foi tirada
    };
  };



  return (
    <div className={`camera ${fotoTirada ? 'sem-before' : ''}`}>
      <div className="top-bar">
        <div className="fechar"><img src={fechar}/></div>
        {!fotoTirada && (
          <div className="flash"><img src={flash}/></div>
        )}
      </div>

      {!fotoTirada && (
        <div className="focus">
          <img src={focar} alt="Focar"/>
        </div>
      )}

{fotoTirada && (
        <Button label="USAR ESSA FOTO" />
      )}

{fotoTirada ? (
        <FooterCam /> // Renderiza FooterCam quando a foto foi tirada
      ) : (
        <div className="footer">
          <div className="outra-foto"></div>
          <div className="tirar-foto" onClick={playSound}><img className="icone" src={icon} alt="Tirar Foto"/></div>
          <div className="inverter-foto"><img src={reverse} alt="Inverter Foto" /></div>
        </div>
      )}

    </div>
  );
}

export default Camera;

 