import React from "react";
import './login.css';
import loginIcon from './img/user.png'
import senhaIcon from './img/senha.png'
import logoface from './img/logo-face.png'
import googlelogo from './img/google-logo.png'

const Login = () => {
    return (
        <div className="login-container">
            <h1>Seu Nutri</h1>

            <div className="social-login">
                <div className="facebook">
                    <img src={logoface} alt="Facebook logo" />
                    <p>Continuar usando Facebook</p>
                </div>

                <div className="google">
                    <img src={googlelogo} alt="Google logo" />
                    <p>Continuar usando Google</p>
                </div>
            </div>

            <div className="or-separator">
                <hr />
                <span>OU</span>
                <hr />
            </div>

            <div className="input2-container">
                <p className="input-label">Login</p>
                <div className="input-with-icon">
                    <img src={loginIcon} alt="User Icon" />
                    <input type="text" />
                </div>
                
                <p className="input-label">Senha</p>
                <div className="input-with-icon">
                    <img src={senhaIcon} alt="Password Icon" />
                    <input type="password" />
                </div>
                <a href="/reset-password" className="forgot-password-link">Esqueceu a senha?</a>
            </div>

            <button type="submit">ENTRAR</button>
            <div className="jatemconta">
                        Não possui uma conta? <span class="texto-entrar">Crie Agora</span>
                    </div>
        </div>
    );
}

export default Login;
