import React, { useState } from 'react';
import './sidebar.css';
import ProfileImage from './imgs/teste.png';
import icone1 from './imgs/icone1.png';
import fecharIcon from './imgs/fechar.png'; // Importe a imagem do ícone de fechar
import Social from './imgs/rede-social.svg'
import Suporte from './imgs/suporte.svg'
import Tour from './imgs/tour.svg'
import User from './imgs/user.svg'
import Fac from './imgs/fac.svg'

const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(true); // Estado para controlar a visibilidade

  // Função para fechar a sidebar
  const handleClose = () => {
    setIsOpen(false);
    setTimeout(() => {
      // Aqui você poderia definir um estado para remover o componente do DOM
      // ou realizar outras ações após o fechamento da sidebar
    }, 500); // Deve corresponder à duração da animação
  };
  

  // Não renderiza a Sidebar se isOpen é false
  if (!isOpen) {
    return null;
  }

  return (
    <div className={`sidebar ${isOpen ? 'slideIn' : 'slideOut'}`}>
      <div className="fechar-btn" onClick={handleClose}>
        <img src={fecharIcon} alt="Fechar" />
      </div>

      <div className="foto-perfil">
        <div className="circle">
          <img src={ProfileImage} alt="Perfil" />
        </div>
      </div>

      <div className="title-info-container">
        <p className="title-info">Pirata Cervejeiro</p>
      </div>

      <div className="email-cell">  
        <p className="info">haxixeiro@haxixe.com</p>
      </div>

  




      <div className="icones">

      <div className="opcao">
        <img src={Tour} />
        <p>Tour pelo App</p>
      </div>

      <div className="opcao">
        <img src={User} />
        <p>Ver Perfil</p>
      </div>

      <div className="opcao">
        <img src={Social} />
        <p>Redes Sociais</p>
      </div>

      <div className="opcao">
        <img src={Suporte} />
        <p>Suporte</p>
      </div>

      <div className="opcao">
        <img src={Fac} />
        <p>FAQ</p>
      </div>

      </div>
    </div>
    
  );
};

export default Sidebar;
