import React from 'react';
import './barra.css';
import { Link } from 'react-router-dom';
import homeIcon from './imgs/home3.png';
import Fac from './imgs/Helps.svg';
import Livro from './imgs/folinha.png';
import Notificacao from './imgs/notificacao.png';
import User from './imgs/Profile.svg';

const Barra = () => {
    return (
        <div className="icon-container">
            <Link to="/home">
                <img src={homeIcon} alt="Home" />
            </Link>

            <Link to="/fac">
                <img src={Fac} alt="Fac" />
            </Link>

            <Link to="/algumaRota">
                <div className="green-circle">
                    <img src={Livro} alt="Livro" />
                </div>
            </Link>

            <Link to="/notificacoes">
                <img src={Notificacao} alt="Notificações" />
            </Link>

            <Link to="/perfil">
                <img className="teste" src={User} alt="Perfil" />
            </Link>
        </div>
    );
};

export default Barra;
