import React, { useState } from 'react';
import hamburgerIcon from './img/hamburguer.png';
import profileIcon from './img/perfil.png';
import Sidebar from '../../components/sidebar';
import './home.css';
import comida from './img/hamburguerIcone.png';
import Barra from '../../components/barra.js';
import Agua from './img/agua.png'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import progresso from './img/progresso.png'

const Home = () => {
  const [isSidebarVisible, setSidebarVisible] = useState(false);

  const toggleSidebar = () => {
    setSidebarVisible(!isSidebarVisible);
    // Forçando a atualização
    setTimeout(() => setSidebarVisible(!isSidebarVisible), 10);
  };
  
  const [activeSlide, setActiveSlide] = useState(0); // Inicia no primeiro slide


  return (
    <>
 
      <div className="header-container">
      <div onMouseDown={toggleSidebar} onTouchStart={toggleSidebar} className="left-icon">
  <div className="icon-wrapper">
    <img src={hamburgerIcon} alt="Hamburger Icon" />
  </div>
</div>

        <div className="title">
          Olá, Usuário!
        </div>
        <div className="right-icon">
          <img src={profileIcon} alt="Profile Icon" />
        </div>
        {isSidebarVisible && <Sidebar />}
      </div>
      
      <div className="algum-texto-aqui"><p>Algum texto Aqui!</p></div>
      <Swiper
  spaceBetween={50}
  slidesPerView={1}
  onSlideChange={(swiper) => setActiveSlide(swiper.realIndex)} // Atualiza o estado do slide ativo
  onSwiper={(swiper) => console.log(swiper)}
>
      <SwiperSlide>
        <div className="veja-dieta-container">
          <div className="dieta-box">
            <p className="dieta-title">VEJA SUA DIETA<br/><span>AQUI</span></p>
            <div className="ver-agora-btn">Ver agora</div>
            <img className="hamburguer-img" src={comida} alt="Hamburguer"/>
          </div>
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="veja-dieta-container">
        <div className="veja-dieta-container">
          <div className="dieta-box">
           
          </div>
        </div>
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="veja-dieta-container">
        <div className="veja-dieta-container">
          <div className="dieta-box">
           
          </div>
        </div>
        </div>
      </SwiperSlide>
    </Swiper>
    <div className="slide-indicators-container">
  {[...Array(3)].map((_, index) => (
    <div
      key={index}
      className={`slide-indicator ${index === activeSlide ? 'active-slide' : ''}`}
    ></div>
  ))}
</div>

<div className="retangulo-container">
    <div className="box">
      <div className="retangulo-agua">
        <img src={Agua} alt="Water Bottle" />
      </div>
      <h2 className="hydration-title">Já bebeu água hoje?</h2>
    </div>
</div>



<div className="retangulo-container">
    <div className="box2">
      <div className="retangulo-progresso">
        <img src={progresso} alt="Water Bottle" />
      </div>
    <h2 className="hydration-title">Marque seu Progresso!</h2>
    </div>
</div>


<div className="retangulo3-container">
    <div className="box3"></div>
</div>



<Barra></Barra>

    </>
  );
  
};

export default Home;
